@font-face {
  font-family: "icons";
  src: url("../resources/icons/icons.ttf") format("truetype"), url("../resources/icons/icons.woff") format("woff"), url("../resources/icons/icons.woff2") format("woff2"), url("../resources/icons/icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=icon-]:before,
[class*=" icon-"]:before {
  font-family: "icons";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^=icon-]:before,
[class*=" icon-"]:before {
  display: block;
}

.icon-basket:before {
  content: "\e900";
}

.icon-phone:before {
  content: "\e901";
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #000;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  background-color: #111111;
  font-size: 18px;
  line-height: 1;
  font-family: "Rubik", sans-serif;
  color: #000;
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Rubik", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
  outline: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: auto;
  overflow: hidden;
}

.main__home {
  padding-top: 10px;
}
.main__promo {
  padding: 110px 0 112px 0;
}
@media (max-width: 765px) {
  .main__promo {
    padding: 80px 0;
  }
}
@media (max-width: 480px) {
  .main__promo {
    padding: 146px 0 30px 0;
  }
}

.container {
  max-width: 1352px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (max-width: 765px) {
  .container {
    padding: 0 10px;
  }
}

@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../Rubik-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: #111111;
  transition: box-shadow 0.3s ease, background-color 0.3s ease;
}
.header.active {
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.85);
}
.header__body {
  min-height: 80px;
  display: flex;
  align-items: center;
  padding: 10px 0;
}
@media (max-width: 1255px) {
  .header__body {
    justify-content: space-between;
  }
}
.header__logo {
  display: block;
  width: 120px;
  margin-right: 7.22%;
}
@media (max-width: 1255px) {
  .header__logo {
    margin-right: 30px;
  }
}
.header__menu {
  margin-right: 5.2777%;
}
@media (max-width: 1255px) {
  .header__menu {
    margin-right: 0;
  }
}
@media (max-width: 992px) {
  .header__menu {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden;
    width: 50%;
    height: 100vh;
    overflow: auto;
    z-index: 50;
    background-color: rgba(0, 0, 0, 0.85);
    transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
    padding: 120px 20px 40px 20px;
  }
  .header__menu.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
  }
}
@media (max-width: 765px) {
  .header__menu {
    width: 70%;
  }
}
@media (max-width: 480px) {
  .header__menu {
    width: 90%;
  }
}
.header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 992px) {
  .header__list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-end;
  }
}
.header__item:not(:last-child) {
  margin-right: 40px;
}
@media (max-width: 1255px) {
  .header__item:not(:last-child) {
    margin-right: 25px;
  }
  .header__item:last-child {
    display: none;
  }
}
@media (max-width: 992px) {
  .header__item:last-child {
    display: block;
  }
  .header__item:not(:last-child) {
    margin: 0 0 30px 0;
  }
}
.header__link {
  position: relative;
  color: #FFFFFF;
}
.header__link::before, .header__link::after {
  content: "";
  position: absolute;
  bottom: -6px;
  width: 0;
  height: 2px;
  opacity: 0;
  background-color: #FFFFFF;
  transition: all 0.3s ease 0s;
}
.header__link::before {
  left: 50%;
}
.header__link::after {
  right: 50%;
}
@media (any-hover: hover) {
  .header__link:hover::before, .header__link:hover::after {
    width: 45%;
    opacity: 1;
  }
}
@media (max-width: 992px) {
  .header__link {
    font-size: 28px;
  }
}
@media (max-width: 480px) {
  .header__link {
    font-size: 25px;
  }
}
.header__actions {
  display: flex;
  align-items: center;
}
.header__phone, .header__basket {
  color: #FFFFFF;
  font-size: 30px;
  transition: color 0.3s ease 0s;
  z-index: 60;
}
@media (any-hover: hover) {
  .header__phone:hover, .header__basket:hover {
    color: #FCB500;
  }
}
.header__phone {
  display: none;
  font-size: 22px;
  transform: rotate(20deg);
}
@media (max-width: 1255px) {
  .header__phone {
    display: block;
    margin-right: 20px;
  }
}
.header__basket {
  position: relative;
  flex: 0 0 32px;
  height: 32px;
}
.header__basket span {
  position: absolute;
  right: -8px;
  bottom: -5px;
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FCB500;
  font-size: 12px;
  color: black;
  border-radius: 100%;
}
@media (max-width: 992px) {
  .header__basket {
    margin-right: 20px;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 992px) {
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 60;
    transition: background-color 0.3s ease 0s;
  }
  .header__burger span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #FFFFFF;
  }
  .header__burger span:first-child {
    top: 0;
  }
  .header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .header__burger.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 992px) and (any-hover: hover) {
  .header__burger:hover span {
    background-color: #FCB500;
  }
}

